import Mescroll  from 'mescroll.js'
import 'mescroll.js/mescroll.min.css'
import { Indicator } from "mint-ui";
import { selectType } from "../../server/employee";
export default {
  name: "client",
  components:{Mescroll },
  data() {
    return {
      imgUrl: require("@/assets/img/seach.png"),
      value: "",
      count: 0,
      mescroll:null,
      page: {
        page: 1,
        size: 50,
        total: 0
      },
      dataList: [] // 列表数据
    };
  },
mounted(){
    this.mescroll=new Mescroll(this.$refs.mescroll,{
      // down:{
      //   callback:this.downCallback
      // },
      up:{
        user:false,
        callback:this.getList,
        noMoreSize:5,
        htmlNodata:'<p class="upwarp-nodata">暂无更多数据---</p>'
      },
      empty:{
        warpId:'mescroll',
        tip:'暂无相关数据哦~~~'
      }
    })
    up:{
      user:false;
    }
  },
  methods: {
    getList() {
    const that = this
      selectType({
        page: this.page.page,
        limit: this.page.size,
        customerName:this.value
      }).then(e => {
        if (e.data.code == 200) {
          var datas = e.data.data;
          if (this.page.page == 1) {
            that.dataList = [];
          }
          var data = datas.list;
          if (data && data.length > 0) {
            that.dataList = that.dataList.concat(data);
          }
        }
        that.$nextTick(() => {
          that.mescroll.endSuccess(data.length);
        });
      });
    },
     handleSeach(){
      console.log(1);
      this.page.page = 1;
      this.getList();
    },
    handleLink(id,customerType){
      localStorage.setItem('customerId',id);
      localStorage.setItem('customerType',customerType);
      this.$router.push({
        path:'/AddEmployee',
        query:{
          customerId:id,
          customerType:customerType
        }
      })
    }
  }
};



